import React from 'react';
import './ColumnHeader.scss';
import { IoIosArrowUp, IoIosArrowDown } from 'react-icons/io';

export default function ColumnHeader({ title, sortable }) {
  const [isSortable, setIsSortable] = React.useState(sortable);
  return (
    <React.Fragment>
      <div className="data-table__col-header">
        <span className="col-header__title">{title}</span>
        <IoIosArrowDown size="1.2em" className="col-header__icon"/>
      </div>
    </React.Fragment>
  );
  
}